.domain-container {
  margin-right: 2%;
  float: left; }

.domain-container.drill-down-open {
  width: 50%; }

.domain-container.drill-down-closed {
  width: 100%; }

.api-button button {
  width: 100%; }

.drill-down {
  width: 45%;
  float: left; }

ul {
  list-style-type: none; }

ul.in-table-list {
  margin: 0;
  padding: 0; }

.drill-down-back {
  float: left; }

.close-drill-down {
  float: right; }

.material-icons.drill-down-back {
  color: green; }

.material-icons.close-drill-down {
  color: red; }

#domain-selector button {
  display: inline-block;
  padding: 0.35em 1.2em;
  border: 0.1em solid #FFFFFF;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.15em;
  box-sizing: border-box;
  font-weight: 500;
  color: #2d3a83;
  text-align: center;
  transition: all 0.2s;
  text-decoration: none; }

#domain-selector button:not(.viewing):hover {
  color: #000000;
  background-color: #FFFFFF; }

.unclickable {
  cursor: default; }

.clickable:hover {
  font-weight: bold;
  cursor: pointer; }

.table-title {
  font-family: 'Germania One', cursive;
  font-size: 40px; }

.got-app {
  background-color: #969696; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  background-color: rgba(150, 150, 150, 1);
}
